import React, { PureComponent } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import Card from "./Elements/Card";
import BrandingCard from "./Elements/BrandingCard";

const PrevArrow = (props) => {
  const { onClick } = props;
  return <div className="tb_hs_arrow tb_hs_arrow_left__ tb__icon tb-arrow-left-alt" onClick={onClick} > </div>
};
const NextArrow = (props) => {
  const { onClick, hasMoreData } = props;
  if (onClick == null && hasMoreData) props.onUpdateData();
  return <div className="tb_hs_arrow tb_hs_arrow_right__ tb__icon tb-arrow-right-alt" onClick={onClick} > </div>
};

export default class StoryTheme extends PureComponent {

  render() {
    const { postData, completeDataObject, wall } = this.props;

    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    const rowCount = wall.ThemeRule.multiRow;

    var settings = {
      infinite: autoScrollStatus === 1 ? false : true,
      lazyLoad: true,
      slidesToShow: numberOfCoumn > 0 ? numberOfCoumn : 5,
      slidesToScroll: 1,
      swipeToSlide: true,
      autoplay: slidePost === 1 ? true : false,
      speed: slidePost === 1 ? (1000) : 500,
      rows: rowCount,
      adaptiveHeight: true,
      autoplaySpeed: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 1500,
      prevArrow: <PrevArrow />,
      nextArrow: <NextArrow />,
      //nextArrow: <NextArrow onUpdateData={this.onUpdateData} hasMoreData={hasMoreData} />,
      responsive: [
        {
          breakpoint: 1200,
          settings: {
            slidesToShow: numberOfCoumn > 0 ? numberOfCoumn - 1 : 4,
            slidesToScroll: numberOfCoumn > 0 ? numberOfCoumn - 1 : 4,
          }
        },
        {
          breakpoint: 991,
          settings: {
            slidesToShow: numberOfCoumn > 0 ? numberOfCoumn - 2 : 3,
            slidesToScroll: numberOfCoumn > 0 ? numberOfCoumn - 2 : 3,
          }
        },
        {
          breakpoint: 560,
          settings: {
            slidesToShow: mobileColumn > 0 ? mobileColumn : 1,
            slidesToScroll: mobileColumn > 0 ? mobileColumn : 1,
          }
        }
      ]
    };

    var BrandingPosition = 4
    return (
      <div className="tb_stt_post_container">
        <Slider {...settings} className="tb_stt_post_slider">
          {
            ((postData && postData.length > 0)) && postData.map((item, index) => {
              const cardData = completeDataObject[item];
              const IsBrandingCard = (BrandingPosition === index) ? true : false
              IsBrandingCard ? BrandingPosition += parseInt(index) + 10 : parseInt(BrandingPosition)
              return <>
                {(IsBrandingCard) ? <BrandingCard key={index} brandingClass={'tb_hs_post_wrapper'} itemIndex={index} personalization={wall.Personalization} /> : null}
                <Card itemData={cardData} key={index} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} />
              </>
            })}
        </Slider>
      </div>
    );
  }
}
