import React, { PureComponent } from "react";
import Card from "./Elements/Card";
import BrandingCard from "./Elements/BrandingCard";

export default class WidgetTheme extends PureComponent {
  render() {
    const { postData, completeDataObject, adjustWidth, wall, clickToShowPopUp } = this.props;
    var BrandingPosition = 4
    return (
      <div className="tb_wt_post_container">
        {
          ((postData && postData.length > 0)) && postData.map((item, index) => {
            const cardData = completeDataObject[item]; const IsBrandingCard = (BrandingPosition === index) ? false : false
            IsBrandingCard ? BrandingPosition += parseInt(index) + 10 : parseInt(BrandingPosition)
            return <>
              {(IsBrandingCard) ?
                <BrandingCard key={index} brandingClass={'tb_wt_post_wrapper'} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} /> : null}
              <Card itemData={cardData} key={index} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} />
            </>
          })
        }
      </div>
    );
  }
}
