import React, { PureComponent } from "react";
import { v4 as uuidv4 } from 'uuid';
import imagesLoaded from 'imagesloaded'
import Isotope from 'isotope-layout';
import Card from "./Elements/Card";
import BrandingCard from "./Elements/BrandingCard";

export default class NewsCard extends PureComponent {
  state = {
    postData: []
  }
  componentDidMount() {
    const { postData } = this.props;
    this.setState({ postData }, () => this.onLoadMasonry())

  }

  onLoadMasonry = () => {
    const { adjustWidth } = this.props;
    if (adjustWidth) {
      var elem = document.querySelector('.tb_nc_post_container');
      var iso = new Isotope(elem, {
        itemSelector: '.tb_nc_post_wrapper',
        layoutMode: 'masonry',
        masonry: {
          columnWidth: 1,
          horizontalOrder: true

        }
      });
      //setTimeout(() => iso.arrange(), 200)
      imagesLoaded(elem, (instance) => {
        iso.layout();
      });
    }
  }
  componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    this.setState({ postData }, () => this.onLoadMasonry())
  }
  render() {
    const { postData, completeDataObject, adjustWidth, wall, postSize, clickToShowPopUp } = this.props;
    var BrandingPosition = 4

    return (
      <div className="tb_nc_post_container">
        {
          ((postData && postData.length > 0)) && postData.map((item, index) => {
            const cardData = completeDataObject[item];
            const IsBrandingCard = (BrandingPosition === index) ? false : false
            IsBrandingCard ? BrandingPosition += parseInt(index) + 10 : parseInt(BrandingPosition)
            return <>
              {(IsBrandingCard) ?
                <BrandingCard key={index} brandingClass={'tb_nc_post_wrapper'} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} /> : null}
              <Card itemData={cardData} key={uuidv4()} itemIndex={index} adjustWidth={adjustWidth} postSize={postSize} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} />
            </>
          })
        }
      </div>
    );
  }
}