import React, { PureComponent, Component } from "react";
import Isotope from 'isotope-layout';
import { v4 as uuidv4 } from 'uuid';
import Card from "./Elements/Card";
import BrandingCard from "./Elements/BrandingCard";

export default class ClassicCard extends PureComponent {
  componentDidMount() {
    var elem = document.querySelector('.tb_gp_post_container');
    var iso = new Isotope(elem, {
      itemSelector: '.tb_gp_post_wrapper',
      layoutMode: 'masonry',
    });
    setTimeout(() => iso.arrange(), 200)
  }
  render() {
    const { postData, completeDataObject, adjustWidth, wall } = this.props;
    var BrandingPosition = 4
    return (
      <div className="tb_gp_post_container">
        {
          ((postData && postData.length > 0)) && postData.map((item, index) => {
            const cardData = completeDataObject[item];
            const IsBrandingCard = (BrandingPosition === index) ? true : false
            IsBrandingCard ? BrandingPosition += parseInt(index)+10 : parseInt(BrandingPosition)
            return <> 
            {( IsBrandingCard) ? <BrandingCard key={index} brandingClass={'tb_gp_post_wrapper'} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization}/> : null}
            <Card itemData={cardData} key={uuidv4()} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule}/>
            </>
          })
        }
      </div>
    );
  }
}
