import React, { PureComponent } from "react";
import reactImageSize from 'react-image-size';
import { NoCameraImgPost } from '../../../../constants'
import { POPUP_IMAGE_RENEW_REQUEST } from '../../../../actions/themeActions'

export default class Image extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      height: '100',
      width: '100',
      error: '',
      imgUrl: props.ImageUrl
    }
  }

  componentDidMount() {
    const { imgUrl } = this.state;
    reactImageSize(imgUrl)
      .then(({ width, height }) => this.setState({
        height: (height * 100) / width,
        width: (width * 100) / height
      }))
      .catch((errorMessage) =>
        this.setState({
          height: 100,
          width: 100,
          error: errorMessage
        })
      )
  }
  onLoad = event => {
    if (event.target.getAttribute("data-load") == 1) this.setState({ imgUrl: event.target.src })
  }
  componentWillReceiveProps(nextProps) {
    const { ImageUrl } = nextProps
    this.setState({ imgUrl: ImageUrl })
  }
  render() {
    const { ImageClass, ImageUrl, item, wallID } = this.props
    const { height, imgUrl } = this.state

    const imageStyle = {
      backgroundImage: `url(${imgUrl}),url(${NoCameraImgPost})`, paddingBottom: height + '%', width: '100%'
    };
    return <>
      <div className={ImageClass} style={imageStyle}> </div>
      <img src={imgUrl} style={{ display: `none` }} data-link={item.link} data-load="0" data-network={item.network.id} data-wall-id={wallID} data-item-id={item.id} data-filter-id={item.filterId} onLoad={this.onLoad} onError={(item.stories == 0) ? (e) => {
        POPUP_IMAGE_RENEW_REQUEST(e)
      } : null} />
    </>
  }
}
