import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { v4 as uuidv4 } from 'uuid';
import { isMobile } from 'react-device-detect';
import NewsCard from "./NewsCard";
import ClassicPhoto from "./ClassicPhoto";
import ClassicCard from "./ClassicCard";
import ModernCard from "./ModernCard";
import SquarePhoto from "./SquarePhoto";
import HorizontalSlider from "./HorizontalSlider"
import HorizontalColumns from "./HorizontalColumns";
import WidgetTheme from "./WidgetTheme";
import GalleryPhoto from "./GalleryPhoto";
import SinglePost from "./SinglePost";
import HighlightTheme from "./HighlightTheme";
import StoryTheme from "./StoryTheme";
import SeeMore from "./Elements/SeeMore/SeeMore";
import { THEME_WIDTH_ADJUSTMENT_MODERN, URLDATA } from '../../utils'
import { AutoScrollThemeEnableLoadData, ShowMoreVisibleThemes } from '../../constants'
import { showPopUP, getDataNextSteps, themePostTracking } from '../../actions/themeActions'


const ThemeRender = ({ wall, postData, adjustWidth, languageSetting, clickToShowPopUp, cardNumber }) => {
  const hasMoreData = postData.hasMoreData[0] ? postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true
  switch (wall.Personalization.widgetTheme) {
    case 20:
      return adjustWidth ? <ModernCard {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} /> : null
    case 5:
      return adjustWidth ? <ClassicCard {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} /> : null
    case 3:
      return adjustWidth ? <ClassicPhoto {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} /> : null
    case 4:
      return adjustWidth ? <SquarePhoto {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} cardNumber={cardNumber} /> : null
    case 19:
      return adjustWidth ? <NewsCard {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} /> : null
    case 50:
      return <GalleryPhoto {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} />
    case 52:
      return <SinglePost {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} />
    case 16:
      return <HorizontalSlider {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} />
    case 55:
      return <HighlightTheme {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} />
    case 49:
      return <WidgetTheme {...postData} hasMoreData={hasMoreData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} />
    case 47:
      return <HorizontalColumns {...postData} wall={wall} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} hasMoreData={hasMoreData} />
    case 60:
      return <StoryTheme {...postData} wall={wall} hasMoreData={hasMoreData} adjustWidth={adjustWidth} languageSetting={languageSetting} clickToShowPopUp={clickToShowPopUp} />
    default:
    // code block
  }
}

class ThemeRoute extends PureComponent {
  state = {
    windowWidth: document.querySelector("#taggbox_main").clientWidth,
    windowHeight: window.innerHeight,
    adjustWidth: 0,
    cardNumber: 0
  }
  componentDidMount() {
    this.onResponsiveTheme();
    window.addEventListener("resize", () => {
      this.setState({ windowWidth: document.querySelector("#taggbox_main").clientWidth, windowHeight: window.innerHeight }, () => this.onResponsiveTheme())
    });
    window.addEventListener("scroll", this.autoLoadScrollData);
  }
  autoLoadScrollData = event => {
    const { wall, postData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    const body = document.body;
    const html = document.documentElement;
    if (wall.Personalization.autoScrollStatus == 1 && AutoScrollThemeEnableLoadData.includes(wall.Personalization.widgetTheme)) {
      const height = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
      if ((window.innerHeight + window.pageYOffset) >= (parseInt(height) - parseInt(50))) {
        if (!(loaderData && loaderData.isShowMoreLoading)) {
          const timeStamp = Math.floor(Date.now() / 1000);
          this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, postData.appendData.networkID, postData.appendData.after, postData.appendData.heightEvent);
        }
      }
    }
  }
  onResponsiveTheme = () => {
    const { wall } = this.props;
    const { windowWidth } = this.state;
    let { adjustWidth, cardNumber } = THEME_WIDTH_ADJUSTMENT_MODERN(windowWidth, wall);
    let postWidth = 100 / windowWidth * adjustWidth;
    adjustWidth = postWidth.toFixed(2);
    this.setState({ adjustWidth, cardNumber })
  }
  onClickToShowPopup = (currentIndex, item) => event => {
    const { wall, languageSetting, postData } = this.props;
    if (isMobile && wall.Personalization.mobilePopup === 0) window.open(item.link, '_blank')
    else {
      let updatePostData = postData.postData.map(pItem => postData.completeDataObject[pItem])
      if (wall.Personalization.postFeatured === 2) window.open(item.link, '_blank')
      else {
        if (wall.Personalization.postFeatured === 1) this.props.showPopUP({ type: 'post', card: item, idArray: updatePostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall })
      }

    }

    themePostTracking({ action: 2, wall: wall.Wall.id, feed: item.feedId, post: item.referenceId ? item.referenceId : item.id })

  }
  render() {
    const { wall, languageSetting, postData } = this.props;


    return <>
      <div className="tb_theme_container">
        <ThemeRender {...this.props} {...this.state} clickToShowPopUp={this.onClickToShowPopup} />
        {wall.Personalization.loadMoreStatus === 1 && ShowMoreVisibleThemes.includes(wall.Personalization.widgetTheme) ? <SeeMore languageSetting={languageSetting} wall={wall} appendData={postData.appendData} postData={postData} /> : null}
      </div>
    </>
  }
}


const mapStateToProps = state => {
  return {
    loaderData: state.loaderData
  }
}
const mapDispatchToProps = dispatch => {
  return {
    showPopUP: (data) => dispatch(showPopUP(data)),
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ThemeRoute);