import React, { PureComponent } from "react";
import { Helmet } from "react-helmet";

export default class AppHead extends PureComponent {

    render() {
        const { wall } = this.props;
        return (
            <Helmet>

                {wall.Personalization.cssStatus === 1 ? <style type="text/css">{wall.Personalization.css}</style> : null}
                {
                    wall.UgcSettings.onsite_status ? <link href={`https://fonts.googleapis.com/css?family=${wall.UgcSettings.onsite_link_font_btn}:${wall.UgcSettings.onsite_font_varient_btn}`} rel="stylesheet" /> : null
                }
                {
                    wall.UgcSettings.onsite_status ? <link href={`https://fonts.googleapis.com/css?family=${wall.UgcSettings.onsite_link_font_msg}:${wall.UgcSettings.onsite_font_varient_msg}`} rel="stylesheet" /> : null
                }
                {
                    wall.ThemeRule && Object.keys(wall.ThemeRule).length > 0 && wall.ThemeRule.link_font && wall.ThemeRule.link_font !== "" ? <link href={`https://fonts.googleapis.com/css?family=${wall.ThemeRule.link_font}:${wall.ThemeRule.font_varient}&display=swap`} rel="stylesheet" /> : null
                }
                {
                    wall.Banner && Object.keys(wall.Banner).length > 0 && wall.Banner.subtitle_link_font !== "" && wall.Banner.subtitle_link_font ? <link href={`https://fonts.googleapis.com/css?family=${wall.Banner.subtitle_link_font}:${wall.Banner.subtitle_font_varient}&display=swap`} rel="stylesheet" /> : null
                }
                {
                    wall.Banner && Object.keys(wall.Banner).length > 0 && wall.Banner.title_link_font !== "" && wall.Banner.title_link_font ? <link href={`https://fonts.googleapis.com/css?family=${wall.Banner.title_link_font}:${wall.Banner.title_font_varient}&display=swap`} rel="stylesheet" /> : null
                }
                {
                    <script async>
                        {`window.fbAsyncInit = function () {
                        FB.init({
                            appId: '117029975354072',
                            xfbml: true,
                            version: 'v12.0'
                        });
                    };`}
                    </script>
                }
                <script async defer crossorigin="anonymous" src="https://connect.facebook.net/en_US/sdk.js"></script>
                {
                    <script async>

                        {`window.twttr = (function (d, s, id) {
      var js, fjs = d.getElementsByTagName(s)[0],
        t = window.twttr || {};
      if (d.getElementById(id)) return t;
      js = d.createElement(s);
      js.id = id;
      js.src = "https://platform.twitter.com/widgets.js";
      fjs.parentNode.insertBefore(js, fjs);

      t._e = [];
      t.ready = function (f) {
                            t._e.push(f);
      };

      return t;
    }(document, "script", "twitter-wjs"));`
                        }
                    </script>
                }


                {
                    (wall.Wall && wall.Wall.google_tacking_status) ? <script async src="https://www.googletagmanager.com/gtag/js"></script> : null
                }
                {
                    (wall.Wall && wall.Wall.google_tacking_status) ? <script async>
                        {`window.dataLayer = window.dataLayer || [];
                    function gtag() {dataLayer.push(arguments); }
                    gtag("js", new Date());
                    window.gaTokenClient = '${wall.Wall.google_tacking_code}';
                    gtag("event", "page_view", {
                        "send_to": window.gaTokenClient,
                    "anonymize_ip": true,
                    });`}

                    </script> : null
                }
            </Helmet>
        );
    }
}
