import React, { Component } from 'react';
import { getWallID } from './utils'
import WidgetApp from './widgetApp';
class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            wall_id: getWallID()
        }
    }

    render() {
        const { wall_id } = this.state;
        return <WidgetApp wallID={wall_id} />
    }
}

export default Main;