import React, { Component } from "react";
import ReactStars from "react-rating-stars-component";
export default class OnsitePost extends Component {
    render() {
        const { item, index, onRemoveImage } = this.props;
        return <div class="tb_onsite_post_wrapper">
            <div class="tb_onsite_submit_post_wrap">
                <div class="tb_onsite_submit_post" style={{ backgroundImage: `url(${item.thumb_file})` }}> </div>
                <div class="tb_onsite_remove_post" onClick={onRemoveImage(index)}>
                    <div className="tb_onsite_remove_ico tb__icon tb-close-alt"> </div>
                </div>
            </div>
            <div class="tb_onsite_post_content">
                <div class="tb_onsite_tab_content">
                    {
                        item.caption && item.caption.length > 0 ? item.caption : item.review && item.review.length > 0 ? item.review : null
                    }
                </div>
                {(item.caption == "" && item.rating) || (item.review && item.review.length > 0) ? <ReactStars
                    edit={false}
                    count={5}
                    size={16}
                    color="#d1d1d1"
                    activeColor="#F8B808"
                    value={item.rating} /> : null}

            </div>
        </div>
    };
}