import React, { Component } from "react";
import { connect } from 'react-redux';
import OnsiteUploader from "./Elements/OnsiteUploader";
import OnSiteThankYou from "./Elements/OnsiteThankYou";
import OnsiteUploadedImage from "./Elements/OnsiteUploadedImage";
import OnsiteForm from "./Elements/OnsiteForm";
import OnsiteNoContent from "./Elements/OnsiteNoContent";
import OnsiteUploaded from './Elements/OnsiteUploaded'
import { onSitePopup, submitPostData } from '../../../../actions/themeActions'


const validateEmail = (email) => {
  const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
}
class OnSiteUpload extends Component {
  state = {
    modalShow: false,
    step: 1,
    wall_id: null,
    name: null,
    email: null,
    upload_image: [],
    receiveMail: false,
    tandc: true,
    nameError: false,
    emailError: false,
    submitErrorMessage: null,
    apiRequest: false
  }
  onShowCloseSubmitPicPopUp = event => {
    const { modalShow } = this.state;
    this.setState({ modalShow: !modalShow }, () => setTimeout(() => this.props.onSitePopup(false), 100))
  }
  componentDidMount() {
    setTimeout(() => this.setState({ modalShow: true }), 100);
  }
  updateImageWithData = (data) => {
    const { upload_image } = this.state;
    if (upload_image && upload_image.length < 3) {
      upload_image.push(data);
      this.setState({ upload_image })
    }
  }
  onUpdateImageAddCaption = (index) => event => {
    const { upload_image } = this.state;
    if (upload_image && upload_image.length > 0 && upload_image[index]) {
      upload_image[index].caption = event.target.value;
      upload_image[index].review = "";
      upload_image[index].rating = 0;
      this.setState({ upload_image })
    }
  }
  onUpdateImageAddReview = (index) => event => {
    const { upload_image } = this.state;
    if (upload_image && upload_image.length > 0 && upload_image[index]) {
      upload_image[index].review = event.target.value;
      upload_image[index].caption = "";
      this.setState({ upload_image })
    }
  }

  onUpdateImageAddRatings = (rating, index) => {
    const { upload_image } = this.state;
    if (upload_image && upload_image.length > 0 && upload_image[index]) {
      upload_image[index].caption = "";
      upload_image[index].rating = rating;
      this.setState({ upload_image })
    }
  }
  onUpdateStep = (step) => event => {
    var isError = false;
    const { upload_image } = this.state;
    if (step == 2) {
      if (upload_image && upload_image.length > 0) {
        upload_image.map((item, index) => {
          if (item.activeTab == 1) {
            if (item.caption == "" || item.caption.length == 0) {
              item.error = true;
              isError = true;
            }
            else item.error = false

          }
          else if (item.activeTab == 2) {
            if (item.review == "" || item.review.length == 0 || item.rating == 0) {
              item.error = true;
              isError = true;
            }
            else item.error = false
          }
        })
      }

    }


    if (!isError) { this.setState({ step, submitErrorMessage: null }) }
    else this.setState({ upload_image })
  }
  onTextName = event => {
    this.setState({ name: event.target.value, nameError: false }, () => {
      if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true })
    })
  }
  onTextEmail = event => {
    this.setState({ email: event.target.value, emailError: false }, () => {
      if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || this.state.email.includes("mailinator")) this.setState({ emailError: true })
    })
  }
  onSubmitData = event => {
    const { name, email, upload_image, tandc, receiveMail, apiRequest } = this.state;
    const { wallId } = this.props;
    if (tandc && (name && name.length > 0) && (email && email.length > 0 && validateEmail(email) && !email.includes("mailinator")) && (upload_image && upload_image.length > 0) && tandc) {
      this.setState({ apiRequest: true })
      submitPostData({ wall_id: wallId, name, email, upload_file: upload_image, receive_mail: receiveMail }).then((response) => {
        this.setState({ step: 3, apiRequest: false }, () => setTimeout(() => {
          if (window.location.href && window.location.href.includes("onsite-upload")) {
            window.location.href = `https://widget.taggbox.com/${wallId}`;
          }
        }, 2000))
      })
        .catch((error) => {

          const { data } = error.response;
          if (data.response_code == 404) {
            this.setState({ submitErrorMessage: data.message, apiRequest: false })
          }
          console.error(error);
        });
    }
    else {
      if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true, apiRequest: false })
      if (this.state.email == null || this.state.email.length == 0 || !validateEmail(this.state.email) || email.includes("mailinator")) this.setState({ emailError: true, apiRequest: false })
    }
  }
  onUpdatereceiveMail = event => {
    const { receiveMail } = this.state;
    this.setState({ receiveMail: !receiveMail })
  }
  onUpdatetandc = event => {
    const { tandc } = this.state;
    this.setState({ tandc: !tandc })
  }
  removeProductImage = (removeIndex) => event => {
    const { upload_image } = this.state;
    if (upload_image && upload_image.length > 0) {
      upload_image.splice(removeIndex, 1);
    }
    this.setState({ upload_image })
  }

  updateActiveTab = (updateIndex, activeTab) => {
    let { upload_image } = this.state;
    if (upload_image && upload_image.length > 0) {
      upload_image.map((item, index) => {
        if (index == updateIndex) {

          upload_image[index].activeTab = activeTab;

        }
      })
    }
    this.setState({ upload_image });
  }


  render() {
    const { wall, onSitePopup } = this.props;
    const { modalShow, step, upload_image, submitErrorMessage, tandc, apiRequest } = this.state;
    return (wall.UgcSettings && Object.keys(wall.UgcSettings).length > 0) ? <div className={`tb_onsite_tb_modal_001 ${modalShow ? `tb_onsite_tb_fade` : ``}`}>
      <div className="tb_onsite_tb_modal_dialog tb_onsite_taggModal">
        {step == 3 ? <OnSiteThankYou onShowCloseSubmitPicPopUp={this.onShowCloseSubmitPicPopUp} UgcSettings={wall.UgcSettings} /> : <div className="tb_onsite_tb_conetent">
          <div className="tb_onsite_tb_modal_body">
            <div className="tb_onsite_tb_close_wrap">
              <div className="tb_onsite_tb_close_btn" onClick={this.onShowCloseSubmitPicPopUp}>
                <div className="tb_onsite_close_ico tb__icon tb-close-alt"> </div>
              </div>
            </div>
            <div className="tb_onsite_tb_modal_body_wrap">
              <div className="tb_onsite_tb_details_left">
                {step == 1 ? <OnsiteUploader updateImageWithData={this.updateImageWithData} UgcSettings={wall.UgcSettings} /> : <OnsiteForm onTextName={this.onTextName} {...this.state} onTextEmail={this.onTextEmail} onUpdatereceiveMail={this.onUpdatereceiveMail} wall={wall} submitErrorMessage={submitErrorMessage} tandc={tandc} UgcSettings={wall.UgcSettings} />}

              </div>
              <div className="tb_onsite_tb_details_right">
                <div className="tb_onsite_tb_content_wrap">
                  <div className="tb_onsite_tb_post_detail_wrap">

                    {
                      upload_image && upload_image.length == 0 ? <OnsiteNoContent /> : step == 1 ? <OnsiteUploadedImage upload_image={upload_image} onUpdateImageAddCaption={this.onUpdateImageAddCaption} onUpdateImageAddReview={this.onUpdateImageAddReview} onUpdateImageAddRatings={this.onUpdateImageAddRatings} onRemoveImage={this.removeProductImage} updateActiveTab={this.updateActiveTab} /> : step == 2 && (upload_image && upload_image.length > 0) ? <OnsiteUploaded upload_image={upload_image} onRemoveImage={this.removeProductImage} /> : null
                    }

                    <div className="tb_onsite_tb_post_footer">
                      <div>
                        {
                          step == 2 ? <div className="tb_onsite_form_btn" onClick={this.onUpdateStep(1)}>Back</div> : null
                        }
                      </div>
                      <div className={`tb_onsite_form_btn ${upload_image.length > 0 ? `` : `submit_pic_disabled`}   ${step == 1 ? `` : apiRequest ? `tb_onsite_spinner` : ``}`} onClick={upload_image.length > 0 ? step == 1 ? this.onUpdateStep(2) : this.onSubmitData : null}>{step == 1 ? `Next` : `${wall.UgcSettings.onsite_popup_btn_txt}`}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        }

      </div>
    </div> : null
  };
}


const mapStateToProps = state => {
  return {
    modalPop: state.modalPop
  }
}
const mapDispatchToProps = dispatch => {
  return {
    onSitePopup: (status) => dispatch(onSitePopup(status)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(OnSiteUpload);