import React, { PureComponent } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import Card from "./Elements/Card";
import BrandingCard from "./Elements/BrandingCard";


const PrevArrow = (props) => {
  const { onClick } = props;
  return <div className="tb_spt_arrow tb_spt_arrow_left__ tb__icon tb-arrow-left-alt" onClick={onClick} > </div>
};
const NextArrow = (props) => {
  const { onClick, hasMoreData } = props;
  if (onClick == null && hasMoreData) props.onUpdateData();
  return <div className="tb_spt_arrow tb_spt_arrow_right__ tb__icon tb-arrow-right-alt" onClick={onClick} > </div>
};
export default class HorizontalColumns extends PureComponent {
  render() {
    const { postData, completeDataObject, wall } = this.props;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    var settings = {
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      swipeToSlide: true,
      autoplay: slidePost === 1 ? true : false,
      speed: 400,
      autoplaySpeed: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 5000,
      prevArrow: <PrevArrow />,
      nextArrow: <NextArrow />,
      //nextArrow: <NextArrow onUpdateData={this.onUpdateData} hasMoreData={hasMoreData} />,
    };
    var BrandingPosition = 4
    return (
      <div className="tb_spt_post_container">
        <Slider {...settings} className="tb_spt_post_slider">
          {
            ((postData && postData.length > 0)) && postData.map((item, index) => {
              const cardData = completeDataObject[item];
              const IsBrandingCard = (BrandingPosition === index) ? false : false
              IsBrandingCard ? BrandingPosition += parseInt(index) + 10 : parseInt(BrandingPosition)
              return <>
                {(IsBrandingCard) ?
                  <BrandingCard key={index} brandingClass={'tb_spt_post_wrapper'} itemIndex={index} personalization={wall.Personalization} /> : null}
                <Card itemData={cardData} key={index} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} />
              </>
            })
          }
        </Slider>
      </div>
    );
  }
}
