import React, { PureComponent } from "react";

export default class CTAButton extends PureComponent {
  render() {
    const { cta, ctaClass, postCta } = this.props;
    const ctaStyle = { color: cta.color, backgroundColor: cta.background }
    return <a className={ctaClass} rel="noreferrer" href={cta.url} target={'_blank'} style={ctaStyle}>{cta.text}</a>
  }
}