import React, { PureComponent } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import PopUpImage from "./PopUpImage";
import PopUpVideo from "./PopUpVideo";

const PrevArrow = (props) => {
  const { onClick } = props;
  return <div className="tb_post_modal_arrow tb_post_modal_arrow_left__ tb__icon tb-arrow-left-alt" onClick={onClick} > </div>
};
const NextArrow = (props) => {
  const { onClick } = props;
  return <div className="tb_post_modal_arrow tb_post_modal_arrow_right__ tb__icon tb-arrow-right-alt" onClick={onClick} > </div>
};
export default class PopUpMediaSlider extends PureComponent {

  render() {
    const { imageList, data, wall } = this.props

    var settings = {
      dots: true,
      dotsClass: "tb_modal_slider_dots",
      infinite: true,
      speed: 500,
      centerMode: true,
      centerPadding: 0,
      slidesToShow: 1,
      slidesToScroll: 1,
      initialSlide: 0,
      mobileFirst: true,
      adaptiveHeight: true,
      prevArrow: <PrevArrow />,
      nextArrow: <NextArrow />
    };

    return <div className="tb_post_modal_media_holder">
      <Slider {...settings} className="tb_post_modal_media_slider">
        {imageList.map((imageItem, index) => <div class="tb_post_modal_media_slider_wrap">

          {(imageItem.type == 2 || imageItem.type == 4) ? <PopUpImage key={`multi-slider${index}`} ImageUrl={imageItem.postFile} data={{ link: imageItem.link, network: { id: imageItem.networkId }, id: imageItem.id, filterId: data.filterId, stories: imageItem.stories }} wall={wall} /> : (imageItem.type === 3 || imageItem.type === 5) ? <PopUpVideo data={{ link: imageItem.link, network: { id: imageItem.networkId }, id: imageItem.id, filterId: data.filterId, stories: imageItem.stories }} wall={wall} /> : null}

        </div>)}
      </Slider>
    </div>
  }
}