import React, { PureComponent } from "react";
import ContentWithOutSlack from './PostContentConvertion'
export default class PostContent extends PureComponent {
  render() {
    const { contentClass, content, ThemeRule, font, personalization, item } = this.props;
    const fontStyle = {
      fontFamily: ThemeRule.css_font,
      fontSize: font.fontsize,
      color: font.fontColor
    };
    return (
      <div className={`${contentClass} tb-sGFfonte-${ThemeRule.font_varient}`} style={fontStyle}>
        <ContentWithOutSlack data={item} content={content} Personalization={personalization} />
      </div>
    );
  }
}
