import React, { PureComponent } from "react";
import Author from "../../Elements/Author/Author";
import Network from "../../Elements/Network/Network";

export default class AuthorDetails extends PureComponent {
  render() {    
    const { author, personalization, postTime, network, ThemeRule, font } = this.props;
    const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
    const authorNameStyle = {
      color: ThemeRule.authorColor
    };
    const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
    return (
          <div class="tb_stt_content_wrap">
            <div class="tb_stt_author_img">
              {postAuthor ? <Author author={author} authorClass={'tb_stt_author_img_00'} /> : '' }
            </div>
            <div class="tb_stt_author_info">
              <Network networkClass={'tb_stt_social_icon'} network={network} ThemeRule={ThemeRule} font={font} />
              <div class="tb_stt_author_001">
                  {postAuthor ? <div className="tb_stt_hashtag sGFfonte-regular" style={authorNameStyle}>{username}</div>  : '' }
                  {postAuthor ? <div className="tb_stt_author_name" style={authorNameStyle}>{author.name}</div> : ''}
              </div>
            </div>
        </div>
    );
  }
}
