import React, { PureComponent } from "react";
import { connect } from "react-redux";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import Card from "./Elements/Card";
import BrandingCard from "./Elements/BrandingCard";
import { getDataNextSteps } from '../../../actions/themeActions'

const PrevArrow = (props) => {
  const { onClick } = props;

  return <div className="tb_hc_arrow tb_hc_arrow_left__ tb__icon tb-arrow-left-alt" onClick={onClick} > </div>
};
const NextArrow = (props) => {
  const { onClick, hasMoreData } = props;
  if (onClick == null && hasMoreData) props.requestData();
  return <div className="tb_hc_arrow tb_hc_arrow_right__ tb__icon tb-arrow-right-alt" onClick={onClick} > </div>
};

class HorizontalColumn extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      height: 0
    }
  }

  requestData = () => {
    const { appendData, wall, preRender, hasMoreData } = this.props;
    if (hasMoreData) {
      this.props.getDataNextSteps(
        wall.Wall.id,
        Math.floor(Date.now() / 1000),
        wall.ThemeRule.numberOfPosts,
        appendData.networkID,
        appendData.after,
        preRender,
        appendData.heightEvent
      );
    }

  };

  componentDidMount() {
    setTimeout(() => {
      const height = this.sliderHeight.clientHeight;
      this.setState({ height });
    }, 100);
  }

  render() {
    const { postData, completeDataObject, wall, clickToShowPopUp, hasMoreData } = this.props;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;

    var settings = {
      infinite: autoScrollStatus === 1 ? false : true,
      lazyLoad: true,
      slidesToShow: numberOfCoumn > 0 ? numberOfCoumn : (postData && postData.length) ? postData.length > 5 ? 5 : postData.length : 5,
      slidesToScroll: numberOfCoumn > 0 ? numberOfCoumn : (postData && postData.length) ? postData.length > 5 ? 5 : postData.length : 5,
      swipeToSlide: true,
      autoplay: slidePost === 1 ? true : false,
      speed: 1500,
      autoplaySpeed: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 1500,
      prevArrow: <PrevArrow />,
      nextArrow: <NextArrow requestData={this.requestData} hasMoreData={hasMoreData} />,
      responsive: [
        {
          breakpoint: 1600,
          settings: {
            slidesToShow: numberOfCoumn > 0 ? numberOfCoumn : (postData && postData.length) ? postData.length > 4 ? 4 : postData.length : 4,
            slidesToScroll: numberOfCoumn > 0 ? numberOfCoumn : (postData && postData.length) ? postData.length > 4 ? 4 : postData.length : 4,
          }
        },
        {
          breakpoint: 1200,
          settings: {
            slidesToShow: numberOfCoumn > 0 ? numberOfCoumn - 1 : (postData && postData.length) ? postData.length > 3 ? 3 : postData.length : 3,
            slidesToScroll: numberOfCoumn > 0 ? numberOfCoumn - 1 : (postData && postData.length) ? postData.length > 3 ? 3 : postData.length : 3,
          }
        },
        {
          breakpoint: 991,
          settings: {
            slidesToShow: numberOfCoumn > 0 ? numberOfCoumn - 2 : (postData && postData.length) ? postData.length > 3 ? 3 : postData.length : 3,
            slidesToScroll: numberOfCoumn > 0 ? numberOfCoumn - 2 : (postData && postData.length) ? postData.length > 3 ? 3 : postData.length : 3,
          }
        },
        {
          breakpoint: 767,
          settings: {
            slidesToShow: numberOfCoumn > 0 ? numberOfCoumn - 3 : (postData && postData.length) ? postData.length > 3 ? 3 : postData.length : 3,
            slidesToScroll: numberOfCoumn > 0 ? numberOfCoumn - 3 : (postData && postData.length) ? postData.length > 3 ? 3 : postData.length : 3,
          }
        },
        {
          breakpoint: 580,
          settings: {
            slidesToShow: mobileColumn > 0 ? mobileColumn : 1,
            slidesToScroll: mobileColumn > 0 ? mobileColumn : 1,

          }
        }
      ]
    };
    var BrandingPosition = 4
    return (
      <div className="tb_hc_post_container" ref={(sliderHeight) => { this.sliderHeight = sliderHeight }}>
        <Slider {...settings} className="tb_hc_post_slider">
          {
            ((postData && postData.length > 0)) && postData.map((item, index) => {
              const cardData = completeDataObject[item];
              const IsBrandingCard = (BrandingPosition === index) ? false : false
              IsBrandingCard ? BrandingPosition += parseInt(index) + 10 : parseInt(BrandingPosition)
              return <>
                {(IsBrandingCard) ? <BrandingCard key={`br_${index}`} brandingClass={'tb_hc_post_wrapper'} itemIndex={index} personalization={wall.Personalization} /> : null}
                <Card itemData={cardData} key={`hr_${index}`} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} sliderHeight={this.state.height} />
              </>
            })}
        </Slider>
      </div>
    );
  }
}


const mapStateToProps = state => {
  return {

  }
}
const mapDispatchToProps = dispatch => {
  return {
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, postData, heightEvent, updateInStateNow)),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(HorizontalColumn);