import React, { PureComponent } from "react";

const SpaceRemove = (image, stingMatch) => {
  if (image && String(image) && String(image).includes(stingMatch)) return image.replace(/\s/g, '')
  else return image
}
export default class Author extends PureComponent {
  constructor(props) {
    super(props)
    this.state = { picture: SpaceRemove(props.author.picture, "ui-avatars") }
  }
  componentWillReceiveProps(nextProps) {
    const { author } = nextProps;
    this.setState({ picture: SpaceRemove(author.picture, "ui-avatars") })
  }
  componentDidMount() {
    const { author } = this.props;
    this.setState({ picture: SpaceRemove(author.picture, "ui-avatars") })
  }
  render() {
    const { author, authorClass } = this.props;
    const { picture } = this.state;
    return <>
      <div className={authorClass} style={{ backgroundImage: `url(${picture})` }}> </div>
      <img src={picture} style={{ display: `none` }} onLoad={event => event.target.src == author.errorPic ? this.setState({ picture: event.target.src.replace(/\s/g, '') }) : null} onError={event => event.target.src = author.errorPic} />
    </>
  }
}
