import React, { PureComponent } from "react";
import {CloudUrl} from "../../../../constants";

export default class BrandingCard extends PureComponent {
  render() {
    const { brandingClass } = this.props;
    const brandingCardUrl = `${CloudUrl}/media/branding-cards/branding-4.svg`
    const brandingCard ={
      backgroundImage: `url('${brandingCardUrl}')`,
      paddingBottom: '100%',
      backgroundSize: 'cover',
      backgroundPosition: 'center',
      backgroundRepeat:'no-repeat',
      border: '1px solid rgba(0, 0, 0, 0.05)'
    }
    return (
        <div className={brandingClass}>
          <div style={brandingCard}> </div>
        </div>
    );
  }
}
