import React, { PureComponent } from "react";
import {CloudUrl} from "../../../../constants";

export default class BrandingCard extends PureComponent {
  render() {
    const { adjustWidth, brandingClass, personalization } = this.props;
    const cardSize = {
      width: `${adjustWidth}px`, padding:personalization.padding/2
    };
    const brandingCardUrl = `${CloudUrl}/media/branding-cards/branding-4.svg`

    const brandingCard ={
      backgroundImage: `url('${brandingCardUrl}')`,
      paddingBottom: '100%',
      backgroundSize: 'contain',
      backgroundPosition: 'center',
      backgroundRepeat:'no-repeat',
      backgroundColor:'#fff'
    }
    return (
        <div style={cardSize} className={brandingClass}>
          <div style={brandingCard}> </div>
        </div>
    );
  }
}
