import React, { PureComponent } from "react";
import AuthorDetails from "./AuthorDetails";
import SinglePostShare from "./SinglePostShare";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import CTAButton from "../../Elements/CTA/CtaButton";
import SinglePostSocialActions from "./SinglePostSocialActions";


export default class Card extends PureComponent {
  render() {
    const { itemData, personalization, adjustWidth, ThemeRule } = this.props;
    const cardStyle = {
      backgroundColor: itemData.font.cardColor
    };
    const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5) ? true : false;

    return (
      <div id={`tb-spt-post-${itemData.id}`} className="tb_spt_post_wrapper" tb-network={itemData.network.id}>
        <div className="tb_spt_post_in" style={cardStyle}>
          <div className="tb_spt_post_media_wrapp">
            {mediaType ? <Media {...itemData} /> : ''}
            <div className="tb_spt_post_details">
              <div className="tb_spt_contant_wrapper">
                <AuthorDetails postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={itemData.font} personalization={personalization} ThemeRule={ThemeRule} />
                <div className="tb_spt_content_wrap_in">
                  {!itemData.hideContent ? <PostContent contentClass={"tb_spt_content"} content={itemData.content} font={itemData.font} ThemeRule={ThemeRule} personalization={personalization} /> : ''}
                  {personalization.ctaStatus ? <div className="tb_spt_post_cta"><CTAButton ctaClass={'tb_spt_post_cta_btn'} personalization={personalization} /> </div> : ''}
                </div>
                <div className="tb_spt_social_actions_container">
                  {itemData.share.status ? <SinglePostShare share={itemData.share} /> : ''}
                  {ThemeRule.socialAction ? <SinglePostSocialActions itemData={itemData} ThemeRule={ThemeRule} /> : ''}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
