import React, { PureComponent } from "react";

export default class WidgetShare extends PureComponent {
  render() {    
    const { share, shareClass } = this.props;
    return (
      <div className={shareClass}>
        <div className="tb_wt_share_wrapper">
          {(share.facebook) && <div className="tb_wt_share_list"><a href={share.facebook} target="_blank" className="tb_wt_share_icon tb__icon tb-facebook"></a></div>}
          {(share.twitter) && <div className="tb_wt_share_list"><a href={share.twitter} target="_blank" className="tb_wt_share_icon tb__icon tb-twitter"></a></div>}
          {(share.linkedin) && <div className="tb_wt_share_list"><a href={share.linkedin} target="_blank" className="tb_wt_share_icon tb__icon tb-linkedin"></a></div>}
        </div>
      </div>
    );
  }
}