import React, { PureComponent } from "react";
import Media from "./Media";
import AuthorDetails from "./AuthorDetails";

export default class Card extends PureComponent {
  render() {    
    const { itemData, personalization, adjustWidth, ThemeRule } = this.props;
    const cardSize = {
      width: `${adjustWidth}px`, padding:personalization.padding/2
    };
    const cardStyle = {
      backgroundColor: itemData.font.cardColor
    };
    const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5 ) ? true : false;
    return (
      <div id={`tb-mc-post-${itemData.id}`} className={`tb_stt_post_wrapper`} style={cardSize} tb-network={itemData.network.icon}>
          <div class="tb_stt_card_wrapper" style={cardStyle}>
            {mediaType ? 
            <div class="tb_stt_media_img">
              <Media {...itemData} /> 
            </div>
            : ''}
            <div class="tb_stt_card_wrapper_00">
                <div class="tb_stt_card_content_0">
                  <AuthorDetails postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={itemData.font} personalization={personalization} ThemeRule={ThemeRule} />
                </div>
            </div>
          </div>
      </div>
    );
  }
}
