import React, { PureComponent } from "react";

export default class OnsiteForm extends PureComponent {
    render() {
        const { onTextName, name, email, receiveMail, onTextEmail, onUpdatereceiveMail, wall, submitErrorMessage, tandc, nameError, emailError } = this.props;

        return <div class="tb_onsite_tb_media_wrap_002">
            <div class="tb_onsite_h3">Fill out the following information</div>
            <div class="tb_onsite_strong">
                <div class="tb_onsite_required">*</div>
                    Indicates required field
                </div>
            <div class="tb_onsite_post_submit_form_wrap">
                <div class="tb_onsite_post_submit_form">
                    <div class="tb_onsite_form_group">
                        <label class="tb_onsite_form_label" for="displayName">
                            Your Name
                                <div class="tb_onsite_required">*</div>
                        </label>
                        <input class="tb_onsite_input_box" type="text" id="displayName" name="firstname" placeholder="Enter your name.." onChange={onTextName} value={name} />
                        {nameError ? <div className="tb_onsite_error_">* Please enter correct your name.</div> : null}
                    </div>
                    <div class="tb_onsite_form_group">
                        <label class="tb_onsite_form_label" for="emailID">
                            Email Address
                                <div class="tb_onsite_required">*</div>
                        </label>
                        <input class="tb_onsite_input_box" type="text" id="emailID" name="lastname" placeholder="Enter your email id.." value={email} onChange={onTextEmail} />
                        {emailError ? <div className="tb_onsite_error_">* Please enter correct email.</div> : null}
                    </div>
                </div>
                <div class="tb_onsite_form_terms">
                    <div class="tb_onsite_email_updates">
                        <label class="tb_onsite_checkbox_" for="receiveMail____">
                            <input type="checkbox" id="receiveMail____" name="receiveMail" checked={receiveMail} onClick={onUpdatereceiveMail} />
                            <div class="tb_onsite_checkbox__in"> </div>
                                I want to receive email updates.
                        </label>
                    </div>
                    <div class="tb_onsite_terms_conditions">
                        By clicking on Submit, you are agreeing to our <a href={wall.UgcSettings.onsite_term_url} target="_blank"> terms and conditions </a>
                        <div class="tb_onsite_required">*</div>
                    </div>
                </div>
            </div>
            {tandc == false ? <div className="tb_onsite_error_" >* Please agree to the terms and conditions to submit your media.</div> : null}


            {submitErrorMessage && submitErrorMessage.length > 0 ? <div className="submit_pic_limit_error">
                <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                    <g id="Stockholm-icons-/-Code-/-Info-circle" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                        <rect id="bound" x="0" y="0" width="24" height="24" />
                        <circle id="Oval-5" fill="#db4b20" opacity="0.3" cx="12" cy="12" r="10" />
                        <rect id="Rectangle-9" fill="#db4b20" x="11" y="10" width="2" height="7" rx="1" />
                        <rect id="Rectangle-9-Copy" fill="#db4b20" x="11" y="7" width="2" height="2" rx="1" />
                    </g>
                </svg>
                {submitErrorMessage}
            </div> : null}
        </div>
    };
}