import React, { PureComponent } from "react";
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import '@splidejs/react-splide/css';
import '@splidejs/react-splide/css/core';
import Card from "./Elements/Card";

const PrevArrow = (props) => {
  const { onClick } = props;
  return <div className="tb_ht_arrow tb_ht_arrow_left__ tb__icon tb-arrow-left-alt" onClick={onClick} > </div>
};
const NextArrow = (props) => {
  const { onClick, hasMoreData } = props;
  if (onClick == null && hasMoreData) props.onUpdateData();
  return <div className="tb_ht_arrow tb_ht_arrow_right__ tb__icon tb-arrow-right-alt" onClick={onClick} > </div>
};

export default class HighlightTheme extends PureComponent {
  onUpdateData = event => {
    console.log("onupdate", event)
  }
  render() {
    const { postData, completeDataObject, wall } = this.props;

    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;
    const mobileColumn = wall.ThemeRule.mobileColumn;
    const slidePost = wall.ThemeRule.slidePost;
    const slideDuration = wall.ThemeRule.slideDuration;
    const autoScrollStatus = wall.Personalization.autoScrollStatus;
    const rowCount = wall.ThemeRule.multiRow;

    // var settings = {
    //   infinite: autoScrollStatus === 1 ? false : true,
    //   lazyLoad: true,
    //   slidesToShow: numberOfCoumn > 0 ? numberOfCoumn : 5,
    //   slidesToScroll: 1,
    //   swipeToSlide: true,
    //   autoplay: slidePost === 1 ? true : false,
    //   speed: slidePost === 1 ? (1000) : 500,
    //   rows: rowCount,
    //   adaptiveHeight: true,
    //   autoplaySpeed: slidePost === 1 ? (parseInt(slideDuration) * 1000) : 1500,
    //   prevArrow: <PrevArrow />,
    //   nextArrow: <NextArrow />,
    //   //nextArrow: <NextArrow onUpdateData={this.onUpdateData} hasMoreData={hasMoreData} />,
    //   responsive: [
    //     {
    //       breakpoint: 1200,
    //       settings: {
    //         slidesToShow: numberOfCoumn > 0 ? numberOfCoumn - 1 : 4,
    //         slidesToScroll: numberOfCoumn > 0 ? numberOfCoumn - 1 : 4,
    //       }
    //     },
    //     {
    //       breakpoint: 991,
    //       settings: {
    //         slidesToShow: numberOfCoumn > 0 ? numberOfCoumn - 2 : 3,
    //         slidesToScroll: numberOfCoumn > 0 ? numberOfCoumn - 2 : 3,
    //       }
    //     },
    //     {
    //       breakpoint: 560,
    //       settings: {
    //         slidesToShow: mobileColumn > 0 ? mobileColumn : 1,
    //         slidesToScroll: mobileColumn > 0 ? mobileColumn : 1,
    //       }
    //     }
    //   ]
    // };
    const settings = {
      direction: 'rtl',
      type: autoScrollStatus === 1 ? false : 'loop',
      rewind: false,
      perPage: numberOfCoumn > 0 ? numberOfCoumn : 5,
      perMove: 1,
      gap: 0,
      autoplay: slidePost === 1 ? true : false,
      padding: "0",
      pagination: false,
      arrows: true,
      breakpoints: {
        560: {
          perPage: 2,
          perMove: 2
        },
        991: {
          perPage: 3,
          perMove: 3
        },
        1200: {
          perPage: 4,
          perMove: 4
        }
      }
    };
    return (
      <div className="tb_ht_post_container">

        <Splide hasTrack={false} className="tb_ht_post_slider" options={settings} ref={this.myRef}>
          <SplideTrack>
            {
              ((postData && postData.length > 0)) && postData.map((item, index) => {
                const cardData = completeDataObject[item];
                return <SplideSlide><Card itemData={cardData} key={index} itemIndex={index} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} /></SplideSlide>
              })}
          </SplideTrack>

          <div className="splide__arrows splide__arrows--rtl">
            <div className="splide__arrow--next tb_hs_arrow tb_hs_arrow_left__ tb__icon tb-arrow-left-alt" ></div>
            <div className="splide__arrow--prev tb_hs_arrow tb_hs_arrow_right__ tb__icon tb-arrow-right-alt" onClick={this.onUpdateData} > </div>
          </div>
        </Splide>
      </div>
    );
  }
}
