import React, { PureComponent } from "react";

export default class ShareOnHover extends PureComponent {

  render() {    
    const { share, shareClass } = this.props;
    return (
      <div className={shareClass}>
          <div className="tb_share_wrapper">
              <div className="tb_share_button_ tb__icon tb-share-hover"> </div>
              <div className="tb_share_icon_list_wrap">
                {share.facebook ? <div className="tb_share_icon_list"><a href={share.facebook} target="_blank" className="tb_share_ico__ tb__icon tb-facebook"></a></div> : ''}
                {/* {share.google ? <div className="tb_share_icon_list"><a href={share.google} target="_blank" className="tb_share_ico__  tb__icon tb-google"></a></div>: ''} */}
                {share.twitter ? <div className="tb_share_icon_list"><a href={share.twitter} target="_blank" className="tb_share_ico__  tb__icon tb-twitter"></a></div>: ''}
                {share.linkedin ? <div className="tb_share_icon_list"><a href={share.linkedin} target="_blank" className="tb_share_ico__ tb__icon tb-linkedin"></a></div>: ''}
              </div>
          </div>
      </div>
    );
  }
}
