import React, { PureComponent } from "react";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import Network from "../../Elements/Network/Network";

export default class Card extends PureComponent {
  render() {    
    const { itemData, personalization, adjustWidth, ThemeRule } = this.props;

    const cardSize = {
      width: `${adjustWidth}px`, padding:personalization.padding/2
    };
    const cardStyle = {
      backgroundColor: itemData.font.cardColor
    };
    const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5 ) ? true : false;
    
    return (
      <div id={`tb-gp-post-${itemData.id}`} className="tb_gp_post_wrapper" style={cardSize} tb-network={itemData.network.id}>
          <a href="#" target="_blank" className="tb_gp_post_in" style={cardStyle}>
            <div className="tb_gp_post_media_wrapp">
                {mediaType ? <Media {...itemData} /> : ''}
                {itemData.network.id != 7 ?
                <div className="tb_gp_contant_">
                  <div className="tb_gp_contant__in">
                    <AuthorDetails postTime={itemData.createdAt} author={itemData.author} font={itemData.font} personalization={personalization} ThemeRule={ThemeRule} />
                    {!itemData.hideContent ? <PostContent contentClass={'tb_gp_content'} content={itemData.content} font={itemData.font} ThemeRule={ThemeRule} /> : ''}
                  </div>
                </div>
                : ''}
                <div className="tb_gp_social_">
                    <Network networkClass={'tb_gp_social__ico'} network={itemData.network} ThemeRule={ThemeRule} font={itemData.font} />
                </div>
            </div>
          </a>
      </div>
    );
  }
}
