import React, { PureComponent } from "react";
import { DragDrop, StatusBar } from '@uppy/react';
import '@uppy/core/dist/style.css'
import '@uppy/drag-drop/dist/style.css'
import '@uppy/status-bar/dist/style.css'
import axios from 'axios'
import { bytesToSize } from '../../../../../utils'
import { VIDEO_COMPRESS } from '../../../../../actions/api'
import UppyUpload from './OnsiteUpload'
import OnSiteLoadingStatus from './OnSiteLoadingStatus'
const requestHeaders = {
    productdomain: `taggbox.com`
}
export default class OnsiteUploader extends PureComponent {
    state = {
        isLoading: false,
        selectedFile: null,
        selectedUrl: null,
        type: null,
        thumb_file: "",
        fileSize: false,
        throwError: false,
        videoPath: "",
        fileSizeText: ""
    }
    beforeFileAdded = (currentFile, files) => {
        const { size, sizeText } = bytesToSize(currentFile.size);
        if (['GB', 'TB'].includes(sizeText)) this.setState({ fileSize: true, fileSizeText: `${size}${sizeText}` }, () => setTimeout(() => this.setState({ fileSize: false, fileSizeText: "" }), 2000))
        else if (sizeText == "MB") {
            if (size >= 100) this.setState({ fileSize: true, fileSizeText: `${size}${sizeText}` }, () => setTimeout(() => this.setState({ fileSize: false, fileSizeText: "" }), 2000))
        }
    }

    beforeUpload = (files) => {
        const fileKeys = Object.keys(files);

        if (fileKeys && fileKeys.length > 0 && files[fileKeys[0]] && files[fileKeys[0]].meta && files[fileKeys[0]].meta.type.includes("video")) {
            try {
                this.setState({ isLoading: true })
                const updateFiles = files[fileKeys[0]];
                const data = new FormData();
                data.append("file", updateFiles.data);
                axios.post(VIDEO_COMPRESS, data, {
                    headers: requestHeaders
                }).then((response) => {
                    const { responseData } = response.data;
                    if (responseData && responseData.length > 0) {
                        this.setState({ videoPath: responseData[0].sscode, isLoading: false, type: 5, selectedFile: responseData[0].s3Url, selectedUrl: responseData[0].s3Url, thumb_file: responseData[0].s3ThumbUrl }, () => this.onUpdateLocalState())
                    }

                }).catch((error) => {
                    this.setState({ isLoading: false, throwError: true }, () => setTimeout(() => this.setState({ throwError: false }), 2000))
                })
            }
            catch (ex) {
                this.setState({ isLoading: false, throwError: true }, () => setTimeout(() => this.setState({ throwError: false }), 2000))
            }
            return false;
        }
    }
    uppyUploader = new UppyUpload(this.beforeFileAdded, this.beforeUpload)
    componentDidMount() {

        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
            this.setState({ type: result.mime.includes("video") ? 5 : 4, selectedFile: result.id, selectedUrl: `https://cloud.taggbox.com/onsiteupload/images/${result.id}` }, () => this.onUpdateLocalState())
        })

    }
    onUpdateLocalState = () => {
        const { updateImageWithData } = this.props;
        const { selectedUrl, type, thumb_file, videoPath } = this.state;
        const data = {
            "file": selectedUrl,
            "caption": "",
            "review": "",
            "rating": 0,
            "error": false,
            "activeTab": 1,
            "type": type,
            "thumb_file": type == 5 ? thumb_file : selectedUrl,
            "videoPath": videoPath
        }
        updateImageWithData(data)
        this.setState({
            selectedFile: null,
            selectedUrl: null,
            type: null,
            thumb_file: "",
            fileSize: false,
            fileSizeText: "",
            videoPath: ""
        }, () => this.uppyUploader.uppy.reset())
    }
    render() {
        const { isLoading, fileSize, throwError, fileSizeText } = this.state;
        const { UgcSettings } = this.props;
        return <div className="tb_onsite_tb_media_wrap_002">
            <div className="tb_onsite_h3">{UgcSettings.onsite_status ? UgcSettings.onsite_popup_title : `Share Your Images With Us`}</div>
            <div className="tb_onsite_p"> {UgcSettings.onsite_status ? UgcSettings.onsite_popup_sub_heading : `Upload your image featuring our brand products and get a chance to feature in our UGC lookbook. `} </div>
            <div className="tb_onsite_uploader_container">
                <div className="tb_onsite_uploader_wrap">
                    <DragDrop
                        className="tb_onsite_uploader"
                        note=""
                        uppy={this.uppyUploader.uppy}
                        allowMultipleFiles={false}

                        locale={{
                            strings: {
                                // Text to show on the droppable area.
                                // `%{browse}` is replaced with a link that opens the system file selection dialog.
                                dropHereOr: '',
                                // Used as the label for the link that opens the system file selection dialog.
                                browse: '',
                            },
                        }}
                    />
                    <div className="tb_onsite_uppy_content">
                        <div className="tb_onsite_uppy_h">Drag a video / image to upload</div>
                        <div className="tb_onsite_uppy_p">Video Size : Max 100MB | Photo Size : Max 50MB | Max 3 Posts</div>
                    </div>
                </div>
                <div className="tb_onsite_uploader_status">
                    <StatusBar
                        uppy={this.uppyUploader.uppy}
                        hideUploadButton
                        hideAfterFinish={false}
                        showProgressDetails
                    />
                    {isLoading ? <OnSiteLoadingStatus /> : null}
                    {fileSize ? <div className="submit_pic_limit_error submit_limit_size" style={{ marginTop: 5, display: `flex`, alignItems: `center` }}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                            <g id="Stockholm-icons-/-Code-/-Info-circle" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                                <rect id="bound" x="0" y="0" width="24" height="24" />
                                <circle id="Oval-5" fill="#db4b20" opacity="0.3" cx="12" cy="12" r="10" />
                                <rect id="Rectangle-9" fill="#db4b20" x="11" y="10" width="2" height="7" rx="1" />
                                <rect id="Rectangle-9-Copy" fill="#db4b20" x="11" y="7" width="2" height="2" rx="1" />
                            </g>
                        </svg>
                    File Size ({fileSizeText}) Limit Exceeded.Video Size : Max 100MB
                </div> : null}
                    {throwError ? <div className="submit_pic_limit_error submit_limit_size" style={{ marginTop: 5, display: `flex`, alignItems: `center` }}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                            <g id="Stockholm-icons-/-Code-/-Info-circle" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                                <rect id="bound" x="0" y="0" width="24" height="24" />
                                <circle id="Oval-5" fill="#db4b20" opacity="0.3" cx="12" cy="12" r="10" />
                                <rect id="Rectangle-9" fill="#db4b20" x="11" y="10" width="2" height="7" rx="1" />
                                <rect id="Rectangle-9-Copy" fill="#db4b20" x="11" y="7" width="2" height="2" rx="1" />
                            </g>
                        </svg>
                     Something went wrong , Please try again.
                </div> : null}

                </div>
            </div>
        </div>
    };
}