import React, { PureComponent } from "react";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from '../../Elements/PostContent/PostContent'
import CTAButton from "../../Elements/CTA/CtaButton";
import Rating from "../../Elements/Rating/Rating";
import SocialAction from "../../Elements/SocialAction/SocialAction";
import { PostTrimContent } from '../../../../utils'

const trim_content_length = 140;
export default class Card extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      mediaHeight: 0
    }
  }

  componentDidMount() {
    setTimeout(() => {
      const mediaHeight = this.mediaHeight.clientWidth;
      this.setState({ mediaHeight });
    }, 100);
  }

  render() {
    const { itemData, personalization, ThemeRule, clickToShowPopUp, itemIndex, wallID, sliderHeight } = this.props;
    const { mediaHeight } = this.state;


    const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5) ? true : false;
    const rating = (itemData.rating > 0 && itemData.network.id !== 29) ? true : false;

    const trimContent = (personalization.trimcontent === 1) ? true : false;
    
    const mediaContentHeight = trimContent ? sliderHeight - mediaHeight : 'auto';
    const txtPostContentHeight = trimContent ? sliderHeight : 'auto';
    const ratingPostContentHeight = trimContent ? sliderHeight - (mediaHeight/2) : 'auto';

    const heightForPost =  itemData.type === 1 ? rating ? ratingPostContentHeight : txtPostContentHeight : mediaContentHeight;

    const cardStyle = {
      backgroundColor: itemData.font.cardColor
    };
    const contentClass = (itemData.rating > 0) ? 'tb_hc_rating_content' : 'tb_hc_content';
    const noMediaClass = (itemData.type === 1) ? 'tb_hc_text_post' : '';
    const textDecorate = (itemData.type === 1) && (personalization.textDecorate) ? 'tb_hc_text_decoration' : '';
    const ctaActive = personalization.cta.ctaStatus || itemData.cta.status ? true : false;
    const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content

    return (
      <div id={`tb-hc-post-${itemData.id}`} className="tb_hc_post_wrapper" onClick={clickToShowPopUp(itemIndex, itemData)}>
        <div className="tb_hc_post_in" style={cardStyle} ref={ (mediaHeight) => { this.mediaHeight = mediaHeight } }>
          {mediaType ? <div className="tb_hc_post_media_wrapp">
            <Media itemData={itemData} wallID={wallID} />
          </div> : null}
          {rating ? <div className={`tb_hc_rating__`} style={{minHeight : rating && trimContent ? mediaHeight/2 : 'auto'}}>
            <Rating rating={itemData.rating} network={itemData.network} /> 
            </div> : null}

          <div className={`tb_hc_contant_wrapper ${noMediaClass}`} style={{minHeight: heightForPost}}>
            <AuthorDetails postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={itemData.font} personalization={personalization} ThemeRule={ThemeRule} rating={itemData.rating}/>
            {ctaActive ? <div className="tb_hc_post_cta">
              <CTAButton ctaClass={'tb_hc_post_cta_btn'} cta={personalization.cta} postCta={itemData.cta} /> </div>
              : ''}
            {!itemData.hideContent ? 
            <div className={`tb_hc_content_wrapper__`}>
              <PostContent contentClass={`${contentClass} ${textDecorate}`} content={postTextContent} font={itemData.font} ThemeRule={ThemeRule} personalization={personalization} item={itemData} />
            </div>
            : null} 

          </div>
          {ThemeRule.socialAction ? <SocialAction itemData={itemData} ThemeRule={ThemeRule} ThemeID={personalization.widgetTheme} actionClass={'tb_hc_social_action__'} /> : ''}
        </div>
      </div>
    );
  }
}
