import React, { Component } from "react";
import OnsitePost from './OnsitePost'

export default class OnsiteUploadedImage extends Component {

    render() {
        const { upload_image } = this.props;
        return <div class="tb_onsite_tb_post_wrapper tb_tb_onsite_product">
            <div class="tb_product_submit_scroll">
                <div class="tb_onsite_products">

                    {
                        upload_image && upload_image.map((item, index) => <OnsitePost item={item} index={index} {...this.props} />)
                    }

                </div>
            </div>
        </div>
    };
}