import React, { PureComponent } from "react";
import Image from "../../Elements/Media/Image";

export default class Media extends PureComponent {
  render() {
    const { ...itemData } = this.props;
    const mediaType = (itemData.type === 3 || itemData.type === 5) ? true : false
    const isYoutubePost = (itemData.network.id === 7) ? true : false
    const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
    const multiImagesClass = ((itemData.imageList && itemData.imageList.length) > 0 && (itemData.type === 3 || itemData.type === 5)) ? 'tb_spt_multi_images_ico_left' : 'tb_spt_multi_images_ico'
    const imageStyle = {
      backgroundImage: `url(${itemData.postFileNew})`, paddingBottom: '130%', width: '100%'
    };

    return <a href="#" target="_blank" className="tb_spt_media_wrap">
      {multiImages ? <div className={`${multiImagesClass} tb__icon tb-multiple`}> </div> : ''}
      {mediaType ? isYoutubePost ? <div className="tb_spt_youtube_ico tb__icon tb-youtube"> </div> : <div className="tb_spt_video_ico tb__icon tb-video"> </div> : '' }
      <div className="tb_spt_image" style={imageStyle}> </div>
    </a>
  }
}
