import React, { PureComponent } from "react";
import Isotope from 'isotope-layout';
import Card from "./Elements/Card";
import BrandingCard from "./Elements/BrandingCard";

export default class SquarePhoto extends PureComponent {
  state = {
    postData: []
  }
  componentDidMount() {
    const { postData } = this.props;
    this.setState({ postData }, () => this.onLoadMasonry())

  }

  onLoadMasonry = () => {
    const { adjustWidth } = this.props;
    if (adjustWidth) {
      var elem = document.querySelector('.tb_sp_post_container');
      var iso = new Isotope(elem, {
        itemSelector: '.tb_sp_post_wrapper',
        layoutMode: 'masonry',
        masonry: {
          columnWidth: 1,
          horizontalOrder: true

        }
      });
      setTimeout(() => iso.arrange(), 200)
    }
  }
  componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    this.setState({ postData }, () => this.onLoadMasonry())
  }
  render() {
    const { postData, completeDataObject, adjustWidth, wall, clickToShowPopUp, cardNumber } = this.props;
    var BrandingPosition = 4
    let totalPostBestFit = postData.length;
    if (wall && wall.Personalization.fitRow == 1) {
      if (postData.length >= parseInt(cardNumber)) {
        let bestFitEachRow = postData.length / parseInt(cardNumber);
        totalPostBestFit = (parseInt(cardNumber) * parseInt(bestFitEachRow));
      }
    }
    return (
      <div className="tb_sp_post_container">
        {((postData && postData.length > 0)) && postData.map((item, index) => {
          const cardData = completeDataObject[item];
          const IsBrandingCard = (BrandingPosition === index) ? false : false
          IsBrandingCard ? BrandingPosition += parseInt(index) + 10 : parseInt(BrandingPosition)
          return parseInt(totalPostBestFit) >= parseInt((index + 1)) ? <>
            {(IsBrandingCard) ? <BrandingCard key={index} brandingClass={'tb_mc_post_wrapper'} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} /> : null}
            <Card itemData={cardData} key={`square_photo_${item.id}`} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} />
          </> : null
        })}
      </div>
    );
  }
}