import React, { PureComponent } from "react";
import imagesLoaded from 'imagesloaded'
import Isotope from 'isotope-layout';
import Card from "./Elements/Card";
import BrandingCard from "./Elements/BrandingCard";

class ModernCard extends PureComponent {
  state = {
    postData: []
  }
  componentDidMount() {
    const { postData } = this.props;
    this.setState({ postData }, () => this.onLoadMasonry())

  }
  onLoadMasonry = () => {
    const { adjustWidth } = this.props;
    if (adjustWidth) {
      var elem = document.querySelector('.tb_mc_post_container');
      var iso = new Isotope(elem, {
        itemSelector: '.tb_mc_post_wrapper',
        layoutMode: 'masonry',
        masonry: {
          columnWidth: 1,
          horizontalOrder: true
        }
      });
      //setTimeout(() => iso.arrange(), 200)
      imagesLoaded(elem, (instance) => {
        iso.layout();
      });
    }
  }
  componentWillReceiveProps(nextProps) {
    const { postData } = nextProps;
    this.setState({ postData }, () => this.onLoadMasonry())
  }
  render() {
    const { completeDataObject, adjustWidth, wall, clickToShowPopUp } = this.props;
    var BrandingPosition = 4
    const { postData } = this.props;

    return (
      <div className="tb_mc_post_container">
        {((postData && postData.length > 0)) && postData.map((item, index) => {
          const cardData = completeDataObject[item];
          const IsBrandingCard = (BrandingPosition === index) ? false : false
          IsBrandingCard ? BrandingPosition += parseInt(index) + 10 : parseInt(BrandingPosition)
          return <>
            {(IsBrandingCard) ?
              <BrandingCard key={`moder_bran_${item.id}`} brandingClass={'tb_mc_post_wrapper'} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} /> : null}
            <Card itemData={cardData} key={`moder_${item.id}`} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} clickToShowPopUp={clickToShowPopUp} wallID={wall.Wall.id} />
          </>
        })}
      </div>
    );
  }
}


export default ModernCard;