import React, { Component } from "react";
import ReactStars from "react-rating-stars-component";
export default class OnsiteUploadedExtends extends Component {
    state = { isActive: 1 }
    componentWillMount() {
        const { item } = this.props;
        this.setState({ isActive: item.activeTab })
    }
    componentDidMount() {
        const { item } = this.props;
        this.setState({ isActive: item.activeTab })
    }
    onUpdateTab = (id) => event => {
        const { index, updateActiveTab } = this.props;

        this.setState({ isActive: id }, () => updateActiveTab(index, id))
    }
    ratingChanged = (newRating) => {
        const { index, onUpdateImageAddRatings } = this.props;
        onUpdateImageAddRatings(newRating, index)
    };
    render() {
        const { item, onUpdateImageAddCaption, onUpdateImageAddReview, onUpdateImageAddRatings, index, onRemoveImage } = this.props;
        const { isActive } = this.state;
        return <div class="tb_onsite_post_wrapper">
            <div class="tb_onsite_submit_post_wrap">
                <div class="tb_onsite_submit_post" style={{ backgroundImage: `url(${item.thumb_file})` }}> </div>
                <div class="tb_onsite_remove_post" onClick={onRemoveImage(index)}>
                    <div className="tb_onsite_remove_ico tb__icon tb-close-alt"> </div>
                </div>
            </div>
            <div class="tb_onsite_post_content">
                <div class="tb_onsite_tab_btn_wrap">
                    <div class={`tb_onsite_tab_btn ${isActive == 1 ? 'tb_onsite_active_btn' : ''}`} onClick={this.onUpdateTab(1)}>Add Caption</div>
                    <div class="tb_onsite_tab_txt">OR</div>
                    <div class={`tb_onsite_tab_btn ${isActive == 2 ? 'tb_onsite_active_btn' : ''}`} onClick={this.onUpdateTab(2)}>Submit Review</div>
                </div>
                <div class="tb_onsite_tab_content" style={isActive == 1 ? { display: 'block' } : { display: 'none' }}>
                    <textarea class="tb_onsite_content_input" placeholder="Add your caption here...." rows="1" onChange={onUpdateImageAddCaption(index)} value={item.caption}>{item.caption}</textarea>
                    {item.error && item.activeTab == 1 ? <div className="tb_onsite_error_">* Please add caption.</div> : null}
                </div>
                <div class="tb_onsite_tab_content" style={isActive == 2 ? { display: 'block' } : { display: 'none' }}>
                    <textarea class="tb_onsite_content_input" placeholder="Add your caption here...." rows="1" onChange={onUpdateImageAddReview(index)} value={item.review}>{item.review}</textarea>
                    {isActive == 2 ? <ReactStars
                        count={5}
                        onChange={this.ratingChanged}
                        size={20}
                        color="#d1d1d1"
                        activeColor="#F8B808"
                        value={item.rating}
                    /> : null}
                    {item.error && item.activeTab == 2 ? <div className="tb_onsite_error_">* Please add review and ratings.</div> : null}
                </div>
            </div>
        </div>
    };
}